import React from 'react';

import { EnactiveMetadata } from '@enactive-ac/integration-ui/src/domain/EnactiveMetadata';
import { MyLocation } from '@mui/icons-material';
import { IconButton } from '@mui/material';
import { ProtoframePubsub } from 'protoframe';

import { selectElement } from '../protocol/SelectElementProtocol';

export type SelectElementLinkProps = {
  href: string;
  onSelectElement: (
    xpath: string,
    element: { outerHtml: string; screenshot: string },
    context: EnactiveMetadata | undefined,
  ) => void;
  window?: Window;
};

export const SelectElementLink: React.FC<SelectElementLinkProps> = ({
  href,
  onSelectElement,
  window = global.window,
}) => {
  const [targetWindow, setTargetWindow] = React.useState<Window>();

  // Store this as a ref because otherwise we'll need to setup and teardown the
  // connector each time the user's context changes which will probably cause
  // periods of time of inactive communication
  const contextRef = React.useRef<EnactiveMetadata>();

  function handleClick(ev: React.MouseEvent) {
    ev.preventDefault();
    setTargetWindow(window.open(href, '_blank') ?? undefined);
  }

  React.useEffect(() => {
    if (targetWindow !== undefined) {
      const connector = new ProtoframePubsub(selectElement, targetWindow);
      connector.handleAsk('onMetadataChanged', async ({ metadata }) => {
        contextRef.current = metadata;
        return {};
      });

      connector.handleAsk('onSelectElement', async ({ selector, element }) => {
        onSelectElement(selector, element, contextRef.current);
        setTargetWindow(undefined);
        return {};
      });
      return () => {
        setTimeout(() => connector.destroy(), 1000);
      };
    }
  }, [onSelectElement, targetWindow]);

  return (
    <IconButton onClick={handleClick} size="small" sx={{ m: 1 }}>
      <MyLocation />
    </IconButton>
  );
};
