import React, { useState } from 'react';

import type { Site } from '@enactive-ac/api/src/domain/Site';
import { SiteAuth } from '@enactive-ac/api/src/domain/SiteAuth';
import type { SiteTheme } from '@enactive-ac/api/src/domain/SiteTheme';
import { Box, Paper, Slide, styled, Tab, TabProps, Tabs } from '@mui/material';
import { Formik } from 'formik';
import { useNavigate } from 'react-router-dom';
import { SchemaOf } from 'yup';

import { useManagementService, useSite } from '../services/ManagementService';
import yup from '../validation/yup';
import { BodyContainer } from './BodyContainer';
import { Button } from './Button';
import { Flex, FlexItem } from './Flex';
import { SiteEdit } from './SiteSettingsScreen/SiteEdit';
import { SiteSettings } from './SiteSettingsScreen/SiteSettings';
import { ThemeSettings } from './SiteSettingsScreen/ThemeSettings';

export const SiteSettingsScreenContainer = styled(BodyContainer)({
  width: '100%',
});

type TabPanelProps = {
  index: number;
  selected: number;
};

const authSchema: SchemaOf<SiteAuth> = yup.object().shape({
  type: yup.mixed<'passthrough'>().oneOf(['passthrough']).required(),
});

const themeSchema: SchemaOf<SiteTheme> = yup.object().shape({
  primaryColor: yup
    .string()
    .required()
    .matches(/^#([a-f0-9]{3}|[a-f0-9]{6})$/i, {
      message: 'Please specify a valid hex colour (e.g., #4f4f4f)',
    }),
  primaryContrastText: yup
    .string()
    .required()
    .matches(/^#([a-f0-9]{3}|[a-f0-9]{6})$/i, {
      message: 'Please specify a valid hex colour (e.g., #FFF)',
    }),
});

const siteSchema: SchemaOf<SiteEdit> = yup.object().shape({
  auth: authSchema.nullable(),
  domain: yup.string().required(),
  platformId: yup.mixed<'ultra' | null>().oneOf(['ultra', null]),
  theme: themeSchema.nullable(),
});

const TabPanel: React.FC<React.PropsWithChildren<TabPanelProps>> = ({
  index,
  selected,
  children,
}) => {
  return (
    <div
      aria-labelledby={`site-settings-tab-${index}`}
      hidden={selected !== index}
      id={`site-settings-tabpanel-${index}`}
      role="tabpanel"
    >
      {selected === index ? <Box sx={{ p: 3 }}>{children}</Box> : null}
    </div>
  );
};

function a11yProps(index: number): Partial<TabProps> {
  return {
    'aria-controls': `vertical-tabpanel-${index}`,
    id: `vertical-tab-${index}`,
  };
}

export const SiteSettingsScreen: React.FC = () => {
  const navigate = useNavigate();
  const [selectedTab, setSelectedTab] = useState(0);

  const managementService = useManagementService();
  const site = useSite();

  const [siteEdit, setSiteEdit] = useState<SiteEdit>();
  React.useEffect(() => {
    if (site === null) {
      setSiteEdit(mkSiteEdit());
    } else if (site !== undefined) {
      setSiteEdit(mkSiteEdit(site));
    }
  }, [site]);

  function handleTabChange(_: React.SyntheticEvent, index: number) {
    setSelectedTab(index);
  }

  const tabs = React.useMemo(
    () => (
      <Tabs
        onChange={handleTabChange}
        orientation="vertical"
        sx={theme => ({
          '& button': {
            alignItems: 'flex-end',
          },
          margin: theme.spacing(4, 0),
        })}
        value={selectedTab}
      >
        {['Site details', 'Theme'].map((label, i) => (
          <Tab
            key={i}
            label={label}
            {...a11yProps(i)}
            sx={theme => ({ paddingX: theme.spacing(4) })}
          />
        ))}
      </Tabs>
    ),
    [selectedTab],
  );

  if (site === undefined || siteEdit === undefined) {
    return null;
  }

  return (
    <SiteSettingsScreenContainer leftNav={tabs}>
      <Formik
        enableReinitialize
        initialValues={siteEdit}
        onSubmit={async (values, actions) => {
          actions.setSubmitting(true);

          const toSave: SiteEdit = {
            ...values,
            platformId:
              (values.platformId as string) === '' ? null : values.platformId,
          };

          if (site === null) {
            await managementService.createSite({ site: toSave });
            navigate('/');
          } else {
            await managementService.updateSite(site.id, { site: toSave });
          }
          actions.setSubmitting(false);
        }}
        validationSchema={siteSchema}
      >
        {formik => (
          <>
            <TabPanel index={0} selected={selectedTab}>
              <SiteSettings formik={formik} />
            </TabPanel>
            <TabPanel index={1} selected={selectedTab}>
              <ThemeSettings formik={formik} />
            </TabPanel>
            <Slide direction="up" in={formik.dirty} mountOnEnter unmountOnExit>
              <Paper
                elevation={5}
                sx={{
                  bottom: 0,
                  left: 0,
                  position: 'absolute',
                  right: 0,
                }}
              >
                <Flex alignItems="center" justifyContent="flex-end" mx={2}>
                  <FlexItem mx={2} py={4}>
                    <Button onClick={() => formik.resetForm()} size="large">
                      Cancel
                    </Button>
                  </FlexItem>
                  <FlexItem mx={2} py={4}>
                    <Button
                      onClick={() => formik.submitForm()}
                      size="large"
                      variant="contained"
                    >
                      Save
                    </Button>
                  </FlexItem>
                </Flex>
              </Paper>
            </Slide>
          </>
        )}
      </Formik>
    </SiteSettingsScreenContainer>
  );
};

function mkSiteEdit(site?: Site): SiteEdit {
  const siteEdit = {
    auth: null,
    domain: '',
    platformId: null,
    theme: {
      primaryColor: '#4f4f4f',
      primaryContrastText: '#fff',
    },
  };

  if (site === undefined) {
    return siteEdit;
  } else {
    const { id: _, platform, ...siteProfile } = site;
    return {
      ...siteEdit,
      ...siteProfile,
      platformId: platform?.id ?? null,
      theme: {
        ...siteEdit.theme,
        ...siteProfile.theme,
      },
    };
  }
}
