import React from 'react';

import { CssBaseline, GlobalStyles, StyledEngineProvider } from '@mui/material';
import { createRoot } from 'react-dom/client';
import { HashRouter } from 'react-router-dom';

import 'typeface-roboto';
import { AppRoot } from './components/AppRoot';
import { ManagementServiceProvider } from './services/ManagementService';

const rootEl = document.getElementById('enactive-management-root');
if (rootEl === null) {
  throw new Error('Could not find #root element');
}

const root = createRoot(rootEl);
root.render(
  <React.StrictMode>
    <StyledEngineProvider injectFirst>
      <HashRouter>
        <CssBaseline>
          <GlobalStyles
            styles={{
              'html, body, #enactive-management-root': {
                height: '100%',
              },
            }}
          />
          <ManagementServiceProvider>
            <AppRoot />
          </ManagementServiceProvider>
        </CssBaseline>
      </HashRouter>
    </StyledEngineProvider>
  </React.StrictMode>,
);
