import React, { useEffect, useMemo, useState } from 'react';

import { Settings } from '@mui/icons-material';
import BubbleChartIcon from '@mui/icons-material/BubbleChart';
import {
  AppBar,
  Box,
  Button,
  CircularProgress,
  IconButton,
  styled,
  Toolbar,
  Typography,
  useTheme,
} from '@mui/material';
import {
  Link as RouterLink,
  Navigate,
  Route,
  Routes,
  useMatch,
} from 'react-router-dom';

import {
  useManagementService,
  useMe,
  useSite,
} from '../services/ManagementService';
import { AccountDropdown } from './AccountDropdown';
import { ContentEditScreen } from './ContentEditScreen';
import { ContentScreen } from './ContentScreen';
import { InstallationScreen } from './InstallationScreen';
import { SignInScreen } from './SignInScreen';
import { SiteSettingsScreen } from './SiteSettingsScreen';

const MenuContent = styled(Box)(({ theme }) => ({
  '& > a': {
    border: 'solid 0.3em rgba(0, 0, 0, 0)',
    borderLeftWidth: 0,
    borderRadius: 0,
    borderRightWidth: 0,
    height: '100%',
    padding: theme.spacing(0, 4),
  },
  '& > a.active': {
    borderBottomColor: 'currentColor',
    borderBottomWidth: 4,
  },
  '& > a.icon': {
    padding: theme.spacing(0, 2),
  },
  alignItems: 'center',
  display: 'flex',
  flexGrow: 1,
  height: '100%',
}));

const MenuLogo = styled(BubbleChartIcon)(({ theme }) => ({
  height: theme.typography.h5.fontSize,
  marginRight: theme.spacing(1),
  width: theme.typography.h5.fontSize,
}));

const MenuTitle = styled(Typography)(({ theme }) => ({
  marginRight: theme.spacing(4),
}));

const Root = styled(Box)(({ theme }) => ({
  backgroundColor: theme.palette.grey['200'],
  display: 'flex',
  flexDirection: 'column',
  minHeight: '100%',
}));

const RootFullScreen = styled(Root)({
  alignItems: 'center',
  justifyContent: 'center',
  width: '100%',
});

// Add the enactive script so we have enactive integration functionality on our own site
const enactiveScript = document.createElement('script');
enactiveScript.src = `https://${
  window.location.hostname
}/integration/entrypoint.js?_=${Date.now()}`;
enactiveScript.setAttribute(
  'enactive.site-id',
  '8c60a02a-3b36-4cf1-874a-bbf0d68f6e29',
);
enactiveScript.setAttribute('enactive.enabled', 'false');
document.body.appendChild(enactiveScript);

export const AppRoot: React.FC = () => {
  const theme = useTheme();
  const managementService = useManagementService();
  const me = useMe();
  const site = useSite();

  const { viewId } =
    useMatch({ end: false, path: '/sites/:siteId/:viewId' })?.params ?? {};

  const [waitingForSignIn, setWaitingForSignIn] = useState(false);
  const signedIn = useMemo(() => {
    if (me !== null && me !== undefined && !waitingForSignIn) {
      return true;
    } else if (me !== undefined) {
      return false;
    } else {
      return undefined;
    }
  }, [me, waitingForSignIn]);

  useEffect(() => {
    if (me !== undefined) {
      if (me === null) {
        setWaitingForSignIn(true);
        enactiveScript.removeAttribute('enactive.context.user.id');
      } else {
        enactiveScript.setAttribute('enactive.context.user.id', me.id);
      }

      // Don't enable the integration until we have resolved whether or not we are signed in right
      // now, and only enable it if we are running on production host
      if (
        window.location.hostname.indexOf('enactive.ac') !== -1 &&
        window.location.hostname.indexOf('local.enactive.ac') === -1
      ) {
        enactiveScript.setAttribute('enactive.enabled', 'true');
      }
    } else {
      enactiveScript.setAttribute('enactive.enabled', 'false');
    }
  }, [me]);

  return (
    <>
      {signedIn !== undefined ? (
        <Root>
          <AppBar position="static" sx={{ zIndex: 1 }}>
            <Toolbar sx={{ height: '4rem' }}>
              <MenuLogo />
              <MenuTitle variant="h6">Enactive Management</MenuTitle>
              <MenuContent>
                {signedIn && site !== undefined && site !== null ? (
                  <>
                    <Button
                      className={viewId === 'content' ? 'active' : undefined}
                      color="inherit"
                      component={RouterLink}
                      to={`/sites/${site.id}/content`}
                    >
                      Content
                    </Button>
                    <Button
                      className={
                        viewId === 'installation' ? 'active' : undefined
                      }
                      color="inherit"
                      component={RouterLink}
                      to={`/sites/${site.id}/installation`}
                    >
                      Installation
                    </Button>
                  </>
                ) : undefined}
                {signedIn ? (
                  <IconButton
                    className={`icon ${viewId === 'settings' ? ' active' : ''}`}
                    color="inherit"
                    component={RouterLink}
                    size="large"
                    sx={{ marginLeft: 'auto', paddingX: 0 }}
                    title="Settings"
                    to={`/sites/${site?.id ?? 'new'}/settings`}
                  >
                    <Settings />
                  </IconButton>
                ) : undefined}
              </MenuContent>
              {(() => {
                if (!signedIn) {
                  return (
                    <Button
                      color="inherit"
                      component={RouterLink}
                      sx={{
                        display: 'flex',
                        height: '100%',
                        paddingX: theme.spacing(2),
                      }}
                      to="/signin"
                    >
                      Sign In
                    </Button>
                  );
                } else if (me !== null && me !== undefined) {
                  return (
                    <AccountDropdown
                      buttonProps={{
                        sx: theme => ({
                          display: 'flex',
                          height: '100%',
                          marginLeft: theme.spacing(2),
                        }),
                      }}
                      me={me}
                      onSignOut={() => managementService.logout()}
                    />
                  );
                }
              })()}
            </Toolbar>
          </AppBar>
          <Routes>
            <Route
              element={
                !signedIn ? (
                  <SignInScreen onSignIn={() => setWaitingForSignIn(false)} />
                ) : (
                  <Navigate replace to="/" />
                )
              }
              path="/signin"
            />
            <Route
              element={
                !signedIn ? (
                  <Navigate replace to="/signin" />
                ) : (
                  <Routes>
                    {/** Redirects */}
                    <Route
                      element={
                        <Navigate
                          replace
                          to={
                            site !== undefined && site !== null
                              ? `/sites/${site.id}/content`
                              : '/sites/new/settings'
                          }
                        />
                      }
                      path="/"
                    />

                    {/** Content screens */}
                    <Route
                      element={<ContentScreen />}
                      path="/sites/:siteId/content"
                    />
                    <Route
                      element={<ContentEditScreen />}
                      path="/sites/:siteId/content/:contentId"
                    />

                    {/** Installation screens */}
                    <Route
                      element={<InstallationScreen />}
                      path="/sites/:siteId/installation"
                    />

                    {/** Settings screens */}
                    <Route
                      element={<SiteSettingsScreen />}
                      path="/sites/:siteId/settings"
                    />
                  </Routes>
                )
              }
              path="/*"
            />
          </Routes>
        </Root>
      ) : (
        <RootFullScreen>
          <CircularProgress />
        </RootFullScreen>
      )}
    </>
  );
};
