import React from 'react';

import {
  Box,
  FormControl,
  FormHelperText,
  InputLabel,
  MenuItem,
  SelectChangeEvent,
} from '@mui/material';
import { Field, FormikProps } from 'formik';
import { Select, TextField } from 'formik-mui';

import { SiteEdit } from './SiteEdit';

export type SiteSettingsProps = {
  formik: FormikProps<SiteEdit>;
};

export const SiteSettings: React.FC<SiteSettingsProps> = ({ formik }) => {
  return (
    <>
      <Box my={2}>
        <Field
          component={TextField}
          fullWidth
          helperText="The primary site that enactive content will be displayed on"
          label="Domain name"
          name="domain"
          placeholder="mysite.example.com"
          size="large"
          validate
        />
      </Box>
      <Box my={2}>
        <FormControl fullWidth>
          <InputLabel
            htmlFor="select-platform-id"
            shrink
            sx={theme => ({
              backgroundColor: theme.palette.background.default,
            })}
          >
            <Box px={1}>Platform</Box>
          </InputLabel>
          <Field
            component={Select}
            displayEmpty
            id="select-platform-id"
            name="platformId"
            onChange={(ev: SelectChangeEvent) =>
              formik.setFieldValue('platformId', ev.target.value)
            }
            value={formik.values.platformId ?? ''}
          >
            <MenuItem
              selected={(formik.values.platformId ?? '') === ''}
              value=""
            >
              Generic
            </MenuItem>
            <MenuItem value="ultra">Anthology Ultra</MenuItem>
          </Field>
          <FormHelperText>
            The application running on your site, if any
          </FormHelperText>
        </FormControl>
      </Box>
    </>
  );
};
