import React from 'react';

import { Box, TextFieldProps, useTheme } from '@mui/material';
import { Field, FormikProps } from 'formik';
import { TextField } from 'formik-mui';

import { Flex } from '../Flex';
import { SiteEdit } from './SiteEdit';

export type ThemeSettingsProps = {
  formik: FormikProps<SiteEdit>;
};

const ColorPicker: React.FC<TextFieldProps & { value?: string }> = ({
  value,
  ...props
}) => {
  return (
    <Flex>
      <Box
        alignSelf="flex-start"
        border="solid 1px #eee"
        borderRadius="0.25rem"
        height="3rem"
        mr={1}
        mt={0.5}
        sx={{ backgroundColor: value }}
        width={'3.3rem'}
      />
      <Field
        size="medium"
        variant="outlined"
        {...props}
        component={TextField}
      />
    </Flex>
  );
};

export const ThemeSettings: React.FC<ThemeSettingsProps> = ({ formik }) => {
  const theme = useTheme();
  return (
    <>
      <Box my={2}>
        <ColorPicker
          fullWidth
          helperText="Used for primary button background. This is usually your primary brand color."
          label="Primary color"
          name="theme.primaryColor"
          placeholder={theme.palette.primary.main}
          value={formik.values.theme?.primaryColor}
        />
      </Box>
      <Box my={2}>
        <ColorPicker
          fullWidth
          helperText="Text color that is displayed on top of your primary color."
          label="Primary contrast text"
          name="theme.primaryContrastText"
          placeholder={theme.palette.primary.contrastText}
          value={formik.values.theme?.primaryContrastText}
        />
      </Box>
    </>
  );
};
