import React, { ReactElement } from 'react';

import { Box, Container, ContainerProps, styled } from '@mui/material';

const ContainerWithoutNav = styled(Container)(({ theme }) => ({
  alignItems: 'flex-start',
  backgroundColor: theme.palette.background.paper,
  boxShadow: theme.shadows[10],
  display: 'flex',
  flexDirection: 'column',
  flexGrow: 1,
  overflowX: 'hidden',
  position: 'relative',
  width: '100%',
}));

const ContainerWithNav = styled(Box)({
  alignItems: 'stretch',
  display: 'flex',
  flexDirection: 'row',
  flexGrow: 1,
  height: '100%',
  justifyContent: 'center',
  width: '100%',
});

const LeftNav = styled(Box)({
  flexGrow: 0,
  flexShrink: 0,
  height: '100%',
  position: 'relative',
});

const BodyWithNav = styled(Container)(({ theme }) => ({
  backgroundColor: theme.palette.background.paper,
  boxShadow: theme.shadows[10],
  display: 'flex',
  flexDirection: 'column',
  flexGrow: 1,
  margin: theme.spacing(0, 4),
  overflowX: 'hidden',
  position: 'relative',
}));

export type BodyContainerProps = ContainerProps & {
  leftNav?: ReactElement;
};

export const BodyContainer: React.FC<BodyContainerProps> = ({
  children,
  leftNav,
  ...ContainerProps
}) => {
  if (leftNav === undefined) {
    return (
      <ContainerWithoutNav maxWidth="md" {...ContainerProps}>
        {children}
      </ContainerWithoutNav>
    );
  }

  return (
    <ContainerWithNav>
      <LeftNav>{leftNav}</LeftNav>
      <BodyWithNav maxWidth="md" {...ContainerProps}>
        {children}
      </BodyWithNav>
      <LeftNav sx={{ visibility: 'hidden' }}>{leftNav}</LeftNav>
    </ContainerWithNav>
  );
};
