import React from 'react';

import { useParams } from 'react-router-dom';

export function useRequiredParams<PS extends string[]>(
  ...ps: PS
): { [key in PS[number]]: string } {
  const raw = useParams<Record<string, string>>();
  const params = React.useMemo<{ [key in PS[number]]: string }>(() => {
    const obj: Record<string, string> = {};
    for (const key of ps) {
      const param = key as PS[number];
      const value = raw[param];
      if (value !== undefined) {
        obj[param] = value;
      } else {
        throw new Error(`parameter "${param}" not available`);
      }
    }
    return obj as { [key in PS[number]]: string };
  }, [raw, ps]);
  return params;
}
