import React from 'react';

import { htmlEncode } from '../util/HtmlUtil';

export function useInputWidth({ window = global } = {}): [
  (el: HTMLElement | null) => void,
  number | undefined,
] {
  const [el, setEl] = React.useState<HTMLElement>();
  const [textWidth, setTextWidth] = React.useState<number>();

  React.useEffect(() => {
    if (el === undefined) {
      return;
    }

    const run = () => {
      const input = el.querySelector('input');
      if (input === null) {
        setTextWidth(undefined);
        return;
      }

      const style = window.getComputedStyle(input);
      const test = document.createElement('span');
      test.style.fontSize = style.fontSize;
      test.style.fontFamily = style.fontFamily;
      test.style.fontStyle = style.fontStyle;
      test.style.fontWeight = style.fontWeight;
      test.style.position = 'absolute';
      test.style.left = '-999px';
      test.style.top = '-999px';
      test.style.visibility = 'hidden';
      test.style.pointerEvents = 'none';
      test.innerHTML = htmlEncode(input.value).replaceAll(/ /g, '&nbsp;');
      test.setAttribute('aria-hidden', 'true');
      input.parentNode?.appendChild(test);

      const width = test.getBoundingClientRect().width;
      setTimeout(() => setTextWidth(width));
      return true;
    };

    el.addEventListener('change', run);
    el.addEventListener('input', run);
    run();
    return () => {
      el.removeEventListener('change', run);
      el.removeEventListener('input', run);
    };
  }, [el, window]);

  const ref = React.useCallback((e: HTMLElement | null) => {
    setEl(e ?? undefined);
  }, []);

  return [ref, textWidth];
}
