import React, { useState } from 'react';

import type { Account } from '@enactive-ac/api/src/domain/Account';
import { Box, Slide } from '@mui/material';

import { useManagementService, useSites } from '../services/ManagementService';
import { BodyContainer } from './BodyContainer';
import { FeatureContainer } from './FeatureContainer';
import { CodeForm } from './SignInScreen/CodeForm';
import { EmailForm } from './SignInScreen/EmailForm';

export type SignInScreenProps = {
  onSignIn: () => void;
};

export const SignInScreen: React.FC<SignInScreenProps> = ({ onSignIn }) => {
  const managementService = useManagementService();
  const [emailEntered, setEmailEntered] = useState(false);
  const [signUpAccount, setSignUpAccount] = useState<Account>();
  const sites = useSites();

  return (
    <BodyContainer>
      <FeatureContainer>
        <Slide
          direction={emailEntered ? 'right' : 'left'}
          in={signUpAccount === undefined}
          onEntered={() => {
            setEmailEntered(true);
            focusInput();
          }}
          unmountOnExit
        >
          <Box position="absolute" width="50%">
            <EmailForm
              managementService={managementService}
              onSignUp={setSignUpAccount}
            />
          </Box>
        </Slide>
        <Slide
          direction={sites === undefined ? 'left' : 'right'}
          in={signUpAccount !== undefined && sites === undefined}
          onEntered={() => focusInput()}
          onExited={() => {
            if (signUpAccount !== undefined && sites !== undefined) {
              onSignIn();
            }
          }}
          unmountOnExit
        >
          <Box position="absolute" width="50%">
            <CodeForm
              account={signUpAccount}
              managementService={managementService}
              onCancel={() => setSignUpAccount(undefined)}
            />
          </Box>
        </Slide>
      </FeatureContainer>
    </BodyContainer>
  );
};

function focusInput(selector = 'input[name]'): void {
  document.querySelector<HTMLInputElement>(selector)?.focus?.();
}
