import React, { useEffect } from 'react';

import { ChatBubble, Circle, Delete, Map } from '@mui/icons-material';
import {
  Box,
  Button,
  Stack,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Typography,
  styled,
  IconButton,
} from '@mui/material';
import { Link as RouterLink, useNavigate } from 'react-router-dom';

import { useRequiredParams } from '../hooks/useRequiredParams';
import {
  ManagementService,
  useManagementService,
  useSite,
  useTours,
} from '../services/ManagementService';
import { BodyContainer } from './BodyContainer';
import { FeatureContainer } from './FeatureContainer';
import { FirstAction } from './FirstAction';
import { Flex } from './Flex';

export type ContentScreenProps = {
  managementService: ManagementService;
};

const ContentScreenContainer = styled(BodyContainer)({
  marginLeft: 'auto',
  marginRight: 'auto',
  width: '100%',
});

const ContentTableRow = styled(TableRow)(({ theme }) => ({
  '&:hover': {
    backgroundColor: theme.palette.grey['50'],
    cursor: 'pointer',
  },
}));

const ContentTableBaseCell = styled(TableCell)(({ theme }) => ({
  padding: theme.spacing(1),
}));

const ContentTableStatusCell = styled(ContentTableBaseCell)(({ theme }) => ({
  paddingLeft: theme.spacing(2),
  paddingRight: theme.spacing(2),
  width: 0,
}));
const ContentTableNameCell = styled(ContentTableBaseCell)({ width: '50%' });
const ContentTableSummaryCell = styled(ContentTableBaseCell)({
  maxWidth: 350,
});
const ContentTableActionsCell = styled(ContentTableBaseCell)(({ theme }) => ({
  paddingLeft: theme.spacing(2),
  paddingRight: theme.spacing(2),
  width: 0,
}));

const StepCount: React.FC<{ count: number }> = ({ count }) => (
  <Box sx={{ display: 'inline-flex', opacity: 0.8, position: 'relative' }}>
    <ChatBubble />
    <Typography
      sx={{
        color: '#fff',
        fontSize: '0.7rem',
        position: 'absolute',
        textAlign: 'center',
        top: '2px',
        width: '100%',
      }}
    >
      {count}
    </Typography>
  </Box>
);

export const ContentScreen: React.FC = () => {
  const { siteId } = useRequiredParams('siteId');
  const navigate = useNavigate();

  const managementService = useManagementService();
  const site = useSite(siteId) ?? undefined;
  const tours = useTours();

  useEffect(() => {
    void managementService.loadTours(siteId);
  }, [managementService, siteId]);

  if (site === undefined || tours === undefined) {
    return null;
  }

  if (tours.length === 0) {
    return (
      <ContentScreenContainer>
        <FeatureContainer>
          <FirstAction
            actionsComponent={
              <Stack direction="row" spacing={2}>
                <Button
                  component={RouterLink}
                  size="large"
                  to="new"
                  variant="contained"
                >
                  Create content
                </Button>
              </Stack>
            }
            illustrationComponent={<Map />}
          >
            Create your first custom content experience.
          </FirstAction>
        </FeatureContainer>
      </ContentScreenContainer>
    );
  }

  return (
    <ContentScreenContainer>
      <Flex my={4}>
        <Button component={RouterLink} to="new" variant="contained">
          Create content
        </Button>
      </Flex>
      <Table width="100%">
        <TableHead>
          <TableRow>
            <ContentTableStatusCell />
            <ContentTableNameCell>Name</ContentTableNameCell>
            <ContentTableSummaryCell>Summary</ContentTableSummaryCell>
            <ContentTableActionsCell />
          </TableRow>
        </TableHead>
        <TableBody>
          {tours.map(tour => {
            return (
              <ContentTableRow
                key={tour.id}
                onClick={() => navigate(tour.id, { relative: 'path' })}
              >
                <ContentTableStatusCell>
                  <Circle
                    color={tour.status === 'published' ? 'success' : 'disabled'}
                    sx={{
                      height: '0.6rem',
                      width: '0.6rem',
                    }}
                    titleAccess={tour.status}
                  />
                </ContentTableStatusCell>
                <ContentTableNameCell>{tour.name}</ContentTableNameCell>
                <ContentTableSummaryCell>
                  <Flex flexWrap="nowrap" sx={{ opacity: 0.875 }}>
                    <StepCount count={tour.steps.length} />
                    <Typography
                      sx={theme => ({
                        display: 'inline',
                        marginLeft: theme.spacing(1),
                        overflow: 'hidden',
                        textOverflow: 'ellipsis',
                        whiteSpace: 'nowrap',
                      })}
                      variant="body2"
                    >
                      {getTextFromHtml(tour.steps.map(s => s.html).join(' | '))}
                    </Typography>
                  </Flex>
                </ContentTableSummaryCell>
                <ContentTableActionsCell>
                  <IconButton
                    onClick={ev => {
                      ev.stopPropagation();
                      void managementService.deleteTour(site.id, tour.id);
                    }}
                    size="small"
                  >
                    <Delete />
                  </IconButton>
                </ContentTableActionsCell>
              </ContentTableRow>
            );
          })}
        </TableBody>
      </Table>
    </ContentScreenContainer>
  );
};

function getTextFromHtml(html: string): string {
  try {
    const text =
      new DOMParser()
        .parseFromString(html, 'text/html')
        ?.body.textContent?.trim() ?? '';
    return text === '' ? 'No content' : text;
  } catch (err) {
    return 'No content';
  }
}
