import React, { ReactNode, useEffect, useMemo, useRef, useState } from 'react';

import type { CreateTourRequest } from '@enactive-ac/api/src/domain/CreateTour';
import type { Tour, TourMatch } from '@enactive-ac/api/src/domain/Tour';
import type { TourStatus } from '@enactive-ac/api/src/domain/TourStatus';
import type { TourStep } from '@enactive-ac/api/src/domain/TourStep';
import { EnactiveMetadata } from '@enactive-ac/integration-ui/src/domain/EnactiveMetadata';
import { AddCircleOutline, ChevronLeft, Delete } from '@mui/icons-material';
import {
  AppBar,
  Box,
  BoxProps,
  Button,
  FormControlLabel,
  IconButton,
  Paper,
  PaperProps,
  Stack,
  styled,
  Switch,
  Theme,
  ToggleButton,
  ToggleButtonGroup,
  Toolbar,
  Typography,
  TypographyProps,
} from '@mui/material';
import {
  Field,
  FieldArray,
  FieldArrayRenderProps,
  Form,
  Formik,
  FormikProps,
} from 'formik';
import { TextField } from 'formik-mui';
import { useNavigate } from 'react-router-dom';
import { SchemaOf } from 'yup';

import { useInputWidth } from '../hooks/useInputWidth';
import { useRequiredParams } from '../hooks/useRequiredParams';
import {
  useManagementService,
  useSite,
  useTour,
} from '../services/ManagementService';
import yup from '../validation/yup';
import { ConditionsField } from './ConditionsField';
import { EditorWrapper } from './EditorWrapper';
import { Flex, FlexItem } from './Flex';
import { SelectElementLink } from './SelectElementLink';

type TourEdit = CreateTourRequest['tour'];

const FullHeightForm = styled(Form)({
  display: 'flex',
  flex: 1,
  flexDirection: 'column',
});

const ToolbarButton = styled(Button)(({ theme }) => ({
  marginLeft: theme.spacing(1),
}));

const Surface = styled(Box)(() => ({
  alignItems: 'stretch',
  display: 'flex',
  flex: 1,
  flexDirection: 'row',
  flexWrap: 'nowrap',
  overflowX: 'auto',
  width: '100%',
}));

const SurfaceSection: React.FC<
  React.PropsWithChildren<{
    size?: number;
    title: ReactNode;
    ContainerProps?: BoxProps;
    ContainerOuterProps?: BoxProps;
    TitleProps?: React.PropsWithoutRef<TypographyProps>;
    BodyProps?: BoxProps;
  }>
> = ({
  children,
  size = 1,
  title,
  ContainerProps,
  ContainerOuterProps,
  TitleProps,
  BodyProps,
}) => {
  return (
    <Box
      flexBasis={`${size * 30}%`}
      flexGrow={0}
      flexShrink={0}
      minWidth={`${size * 450}px`}
      mx={1}
      {...ContainerOuterProps}
    >
      <Box
        display="flex"
        flexDirection="column"
        height="100%"
        px={4}
        {...ContainerProps}
      >
        <Typography
          alignItems="center"
          component="h4"
          display="flex"
          flex={0}
          py={2}
          variant="h5"
          {...TitleProps}
        >
          {title}
        </Typography>
        <Box
          alignItems="center"
          display="flex"
          flex={1}
          flexDirection="column"
          py={2}
          {...BodyProps}
        >
          {children}
        </Box>
      </Box>
    </Box>
  );
};

const SurfaceBlock = styled((props: PaperProps) => (
  <Paper elevation={3} {...props} />
))(({ theme }) => ({
  borderRadius: theme.spacing(1),
  marginBottom: theme.spacing(2),
  padding: theme.spacing(2),
  width: '100%',
}));

const selectorSchema: SchemaOf<Required<TourStep['selector']>> = yup
  .object()
  .shape({
    type: yup.mixed<'css' | 'xpath'>().oneOf(['css', 'xpath']).default('css'),
    value: yup
      .string()
      .when('type', {
        is: 'xpath',
        otherwise: yup.string().validCssSelector(),
        then: yup.string().validXpathSelector(),
      })
      .default(''),
  });

const stepSchema: SchemaOf<Omit<TourStep, 'id' | 'buttons'>> = yup
  .object()
  .shape({
    html: yup
      .string()
      .required('Message is required')
      .validNonEmptyHtml({ empty: 'Message requires some text' }),
    mask: yup.boolean().required(),
    name: yup.string(),
    selector: selectorSchema.notRequired(),
  });

const tourMatchSchema: SchemaOf<TourMatch> = yup.object().shape({
  data: yup.object(),
  jsonpath: yup.string().default('').validJsonPathFilter(),
});

const tourSchema: SchemaOf<
  Omit<Tour, 'id' | 'steps'> & { steps: Omit<TourStep, 'id' | 'buttons'>[] }
> = yup.object().shape({
  launch: tourMatchSchema.nullable(true).optional(),
  match: tourMatchSchema,
  name: yup.string().required('Name is required'),
  status: yup
    .mixed<'draft' | 'published'>()
    .oneOf(['draft', 'published'])
    .default('draft'),
  steps: yup.array().of(stepSchema),
});

export const ContentEditScreen: React.FC = () => {
  const navigate = useNavigate();
  const { contentId, siteId } = useRequiredParams('contentId', 'siteId');

  const [titleRef, titleWidthPx] = useInputWidth();

  const isEdit = useMemo(() => contentId !== 'new', [contentId]);
  const [changeStatus, setChangeStatus] = useState<TourStatus>();
  const [lastContext, setLastContext] = useState<EnactiveMetadata>();

  const managementService = useManagementService();
  const site = useSite(siteId) ?? undefined;
  const tour = useTour(contentId);

  const [tourEdit, setTourEdit] = useState<TourEdit>();
  useEffect(() => {
    if (tour === null) {
      setTourEdit(mkTourEdit());
    } else if (tour !== undefined) {
      setTourEdit(mkTourEdit(tour));
    }
  }, [tour]);

  const formRef = useRef<FormikProps<TourEdit>>(null);

  function getPageUrl() {
    const lastPageUrl = lastContext?.context?.page?.url;
    if (lastPageUrl !== undefined) {
      return lastPageUrl;
    }

    return `https://${site?.domain ?? 'www.google.com'}`;
  }

  function handleCreateStep(
    formik: FormikProps<TourEdit>,
    fieldArray: FieldArrayRenderProps,
  ) {
    fieldArray.push(mkTourEditStep());
  }

  function handleSubmit(status?: TourStatus) {
    setChangeStatus(status);
    if (formRef.current !== null) {
      void formRef.current.submitForm();
    }
  }

  function handleClose() {
    navigate(-1);
  }

  if (site === undefined || tourEdit === undefined) {
    return null;
  }

  return (
    <Formik
      enableReinitialize
      initialValues={tourEdit}
      innerRef={formRef}
      onSubmit={async (tourEdit, actions) => {
        actions.setSubmitting(true);

        // Clean the tour. Namely, convert optional empty fields to undefined
        const toSave: TourEdit = {
          ...tourEdit,
          status: changeStatus ?? tour?.status ?? tourEdit.status,
          steps: tourEdit.steps.map(step => ({
            ...step,
            name: step.name?.trim() === '' ? undefined : step.name,
            selector:
              step.selector?.value.trim() === '' ? undefined : step.selector,
          })),
        };

        await (!isEdit
          ? managementService.createTour(site.id, { tour: toSave })
          : managementService.updateTour(site.id, contentId ?? '', {
              tour: toSave,
            }));
        actions.setSubmitting(false);
        handleClose();
      }}
      validateOnChange={false}
      validationSchema={tourSchema}
    >
      {formik => (
        <FullHeightForm>
          <AppBar
            color="transparent"
            position="relative"
            sx={theme => ({ backgroundColor: theme.palette.grey['50'] })}
          >
            <Toolbar>
              <Flex flex={1}>
                <FlexItem mr={2}>
                  <IconButton onClick={handleClose} size="large">
                    <ChevronLeft />
                  </IconButton>
                </FlexItem>
                <FlexItem>
                  <Field
                    InputProps={{
                      ref: (el: HTMLInputElement | null) => titleRef(el),
                      sx: {
                        fontSize: '1.5rem',
                      },
                    }}
                    component={TextField}
                    fullWidth
                    name="name"
                    placeholder="Title"
                    size="large"
                    sx={{
                      minWidth: '250px',
                      width:
                        titleWidthPx !== undefined
                          ? `${titleWidthPx + 16}px`
                          : undefined,
                    }}
                    validate
                    variant="standard"
                  />
                </FlexItem>
              </Flex>
              <Box>
                <ToolbarButton onClick={handleClose}>Cancel</ToolbarButton>
                <ToolbarButton onClick={() => handleSubmit('draft')}>
                  {!isEdit || tour?.status === 'draft'
                    ? 'Save draft'
                    : 'Save and unpublish'}
                </ToolbarButton>
                <ToolbarButton
                  onClick={() => handleSubmit('published')}
                  variant="contained"
                >
                  {!isEdit
                    ? 'Publish'
                    : tour?.status === 'draft'
                    ? 'Save and publish'
                    : 'Save'}
                </ToolbarButton>
              </Box>
            </Toolbar>
          </AppBar>
          <FieldArray name="steps">
            {fieldArray => (
              <Surface>
                <SurfaceSection
                  TitleProps={{ visibility: 'hidden' }}
                  title="Tour settings"
                >
                  <SurfaceBlock>
                    <Box my={2}>
                      <ConditionsField
                        FieldProps={{
                          base: {
                            fullWidth: true,
                            label: 'Content available to user when...',
                          },
                          search: {
                            helperText:
                              formik.values.match.jsonpath.trim() === ''
                                ? 'Always available'
                                : 'Available when all conditions are met',
                          },
                          text: {
                            helperText:
                              formik.values.match.jsonpath.trim() === ''
                                ? 'Always available'
                                : 'Available when the expression evaluates to true',
                          },
                        }}
                        attrName="match"
                        formik={formik}
                        site={site}
                        tour={tour ?? undefined}
                      />
                    </Box>
                    <Box my={2}>
                      <FormControlLabel
                        control={
                          <Switch
                            checked={
                              typeof formik.values.launch?.jsonpath === 'string'
                            }
                            onChange={() => {
                              if (
                                typeof formik.values.launch?.jsonpath ===
                                'string'
                              ) {
                                formik.setFieldValue('launch', null);
                              } else {
                                formik.setFieldValue('launch', {
                                  jsonpath: '',
                                });
                              }
                            }}
                          />
                        }
                        label="Automatically launch..."
                      />
                      {typeof formik.values.launch?.jsonpath === 'string' ? (
                        <Box my={2} pl={6}>
                          <ConditionsField
                            FieldProps={{
                              base: {
                                fullWidth: true,
                                label: 'Only when...',
                              },
                              search: {
                                helperText:
                                  formik.values.launch.jsonpath.trim() === ''
                                    ? 'Always launch when available'
                                    : 'Launch when all conditions are met',
                              },
                              text: {
                                helperText:
                                  formik.values.launch.jsonpath.trim() === ''
                                    ? 'Always launch when available'
                                    : 'Launch when the expression evaluates to true',
                              },
                            }}
                            attrName="launch"
                            formik={formik}
                            site={site}
                            tour={tour ?? undefined}
                          />
                        </Box>
                      ) : null}
                    </Box>
                  </SurfaceBlock>
                </SurfaceSection>
                {formik.values.steps.map((s, i) => (
                  <SurfaceSection
                    key={s.id === '' ? i : s.id}
                    ContainerProps={{
                      sx: theme => ({
                        backgroundColor: theme.palette.grey['50'],
                        border: `solid 1px ${theme.palette.grey['300']}`,
                      }),
                    }}
                    title={
                      <>
                        <span>{`${i + 1}.`}</span>
                        <Field
                          InputProps={{
                            sx: {
                              fontSize: '1.5rem',
                            },
                          }}
                          component={TextField}
                          fullWidth
                          name={`steps[${i}].name`}
                          placeholder="Unnamed step"
                          size="large"
                          sx={(theme: Theme) => ({
                            ml: theme.spacing(2),
                          })}
                          validate
                          variant="standard"
                        />
                      </>
                    }
                  >
                    <SurfaceBlock>
                      <Box mb={1}>Message content:</Box>
                      <EditorWrapper
                        errorText={
                          formik.getFieldMeta(`steps[${i}].html`).error
                        }
                        onBlur={() => {
                          formik.setFieldTouched(`steps[${i}].html`, true);
                        }}
                        onChange={html => {
                          formik.setFieldValue(`steps[${i}].html`, html);
                        }}
                        value={formik.values.steps[i].html}
                      />
                    </SurfaceBlock>
                    <SurfaceBlock>
                      <Box my={2}>
                        <FormControlLabel
                          control={
                            <Switch
                              checked={formik.values.steps[i].mask}
                              onChange={() => {
                                formik.setFieldValue(
                                  `steps[${i}].mask`,
                                  !formik.values.steps[i].mask,
                                );
                              }}
                            />
                          }
                          label="Modal overlay"
                        />
                      </Box>
                      <Stack direction="row" my={2}>
                        <FlexItem pt="0.2rem">
                          <SelectElementLink
                            href={getPageUrl()}
                            onSelectElement={(selector, element, context) => {
                              // We set the last context so we can more easily
                              // get back there after the first selection
                              setLastContext(c =>
                                context === undefined ? c : context,
                              );
                              formik.setFieldValue(
                                `steps[${i}].selector.type`,
                                'css',
                              );
                              formik.setFieldValue(
                                `steps[${i}].selector.value`,
                                selector,
                              );
                            }}
                          />
                        </FlexItem>
                        <FlexItem grow>
                          <Field
                            component={TextField}
                            fullWidth
                            helperText="Use the CSS/XPath toggle to choose a selector format"
                            label="Attach to element"
                            name={`steps[${i}].selector.value`}
                            size="medium"
                            variant="outlined"
                          />
                        </FlexItem>
                        <FlexItem m={0.5}>
                          <ToggleButtonGroup
                            onChange={() => {
                              // After the value change happens in the form, set the selector
                              // field as touched so it revalidates
                              setTimeout(() =>
                                formik.setFieldTouched(
                                  `steps[${i}].selector.value`,
                                  true,
                                  true,
                                ),
                              );
                            }}
                            value={[
                              formik.values.steps[i].selector?.type ?? 'css',
                            ]}
                          >
                            <ToggleButton
                              onClick={() =>
                                formik.setFieldValue(
                                  `steps[${i}].selector.type`,
                                  'css',
                                )
                              }
                              value="css"
                            >
                              CSS
                            </ToggleButton>
                            <ToggleButton
                              onClick={() =>
                                formik.setFieldValue(
                                  `steps[${i}].selector.type`,
                                  'xpath',
                                )
                              }
                              value="xpath"
                            >
                              XPath
                            </ToggleButton>
                          </ToggleButtonGroup>
                        </FlexItem>
                      </Stack>
                    </SurfaceBlock>
                    <SurfaceBlock
                      elevation={0}
                      sx={{ backgroundColor: 'transparent', border: 'none' }}
                    >
                      <Box textAlign="right">
                        <Button
                          color="error"
                          onClick={() => fieldArray.remove(i)}
                          startIcon={<Delete />}
                          variant="contained"
                        >
                          Delete
                        </Button>
                      </Box>
                    </SurfaceBlock>
                  </SurfaceSection>
                ))}
                <SurfaceSection
                  BodyProps={{
                    alignItems: 'center',
                    justifyContent: 'center',
                  }}
                  ContainerProps={{
                    onClick: () => handleCreateStep(formik, fieldArray),
                    sx: theme => ({
                      '&:hover': {
                        backgroundColor: theme.palette.grey['300'],
                      },
                      cursor: 'pointer',
                    }),
                  }}
                  TitleProps={{ visibility: 'hidden' }}
                  size={0.5}
                  title="Add a step"
                >
                  <Typography mb={2}>Add another step</Typography>
                  <AddCircleOutline
                    color="disabled"
                    sx={theme => ({
                      color: theme.palette.grey['600'],
                      fontSize: '5rem',
                    })}
                  />
                </SurfaceSection>
              </Surface>
            )}
          </FieldArray>
        </FullHeightForm>
      )}
    </Formik>
  );
};

function mkTourEdit(tour?: Tour): TourEdit {
  if (tour === undefined) {
    return {
      match: {
        jsonpath: '',
      },
      name: 'Untitled tour',
      status: 'draft',
      steps: [mkTourEditStep()],
    };
  } else {
    const { id: _, ...t } = tour;
    return {
      ...t,
      match: {
        data: tour.match.data,
        jsonpath: tour.match.jsonpath ?? '',
      },
      steps: tour.steps.map(mkTourEditStep),
    };
  }
}

function mkTourEditStep(step?: TourStep): TourStep {
  if (step === undefined) {
    return {
      html: '',
      id: '',
      mask: false,
      selector: {
        type: 'css',
        value: '',
      },
    };
  } else {
    return step.selector !== undefined
      ? step
      : {
          ...step,
          selector: {
            type: 'css',
            value: '',
          },
        };
  }
}
